.ant-carousel .slick-slide {
  text-align: center;
  height: 250px;
  line-height: 250px;
  background: #364d79;
  overflow: hidden;
}

.ant-carousel .slick-slide h3 {
  color: #fff;
}
.item {
  width: 100%;
  height: 100%;
  background-position: 100% 100%;
  background-repeat: no-repeat;
}