.logo {
  width: 120px;
  height: 31px;
  line-height: 31px;
  margin: 15px auto;
  float: left;
  color: #fff;
  font-size: 18px;
}
.ant-layout{
  background-color: #fff;
}

.comment-list{
  width: 80%;
  margin: 10px auto 0px;
  background-color: rgb(240, 242, 245);
  padding: 20px;
}

.publishBtn{
  margin: 20px auto;
  width: 80%;
  text-align: center;
}

.loadding-icon {
  text-align: center;
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 20px 50px;
  margin: 20px 0;
}
.bottom-tip{
  text-align: center;
  width: 80%;
  margin: 0 auto;
  background-color: rgb(240, 242, 245);
  padding: 20px;
}

.ant-comment-inner {
  padding: 10px 0;
}
.ant-comment-content{
  font-size: 15px;
}
.ant-comment-content-author > a, .ant-comment-content-author > .ant-comment-content-author-name{
  font-size: 15px;
  color: #096dd9;
}